/* ============================================================ //
// @ pagenation
// ============================================================ */

$img_path: "./../../assets/img/";



.pagenavi {
  text-align: center;
  margin-top: 50px;


  .cur,
  .is-current,
  .extend,
  a {
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 10px;
    display: inline-block;
    min-width: 36px;
    height: 36px;
    transition: all $hover-transition ease;
    background-color: #fff;
    line-height: 34px;
    text-align: center;
    text-decoration: none;
    box-sizing: border-box;
    border: 1px solid #ccc;
    font-size: .75em;
    color: $text-color-sub;
  }

  .cur,
  .is-current,
  a:hover {
    border: 1px solid #ccc;
    background: $brand-main-darker;
    color: #fff;
    &:before {
      border-color: #fff;
    }
  }
}

.previouspostslink,
.nextpostslink {
  position: relative;

  &:before {
    display: inline-block;
    position: relative;
    top: -.1em;
    bottom: 0;
    right: 0;
    left: 0;
    width: 4px;
    height: 4px;
    margin: auto;
    transform: rotate(45deg);
    border: 1px solid $text-color-sub;
    content: "";
    vertical-align: middle;
  }
}

.previouspostslink:before {
  border-top-width: 0;
  border-right-width: 0;
  border-bottom-width: 1px;
  border-left-width: 1px;
  left: .1em;
}

.nextpostslink:before {
  border-top-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 0;
  border-left-width: 0;
  right: .1em;
}