$img_path: "./../../assets/img/";

.google-maps {
  position: relative;
  padding-bottom: 75%; // これが縦横比
  height: 0;
  overflow: hidden;
}
.google-maps iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}